barba.init({
  transitions: [
    {
      name: "slide-right",
      leave({ current }: any) {
        return current.container.animate(
          [{ transform: "translateX(0%)" }, { transform: "translateX(-100%)" }],
          { duration: 500, easing: "ease-in-out" }
        ).finished;
      },
      enter({ next }: any) {
        return next.container.animate(
          [{ transform: "translateX(100%)" }, { transform: "translateX(0%)" }],
          { duration: 500, easing: "ease-in-out" }
        ).finished;
      },
    },
  ],
});
